<template>
  <div class="report-history">
    <div class="header">
      <span></span>
      <span></span>
    </div>
    <scroll class="report-list-wrapper"
            v-if="reportList.length"
            :data="reportList"
            :pullup="true"
            @scrollToEnd="getMore"
    >
      <div class="scroll-content">
        <ul class="report-list">
          <li class="item" v-for="item in reportList" :key="item.file_id">
            <span>{{item.time}}</span>
            <a class="link" :href="item.url" :download="`${item.time}.${item.ext}`"></a>
          </li>
        </ul>
        <load-more :over="over" :is-loading="isLoading"/>
      </div>
    </scroll>
    <!--  -->
    <div class="tips" v-if="!reportList.length"></div>
  </div>
</template>

<script>
import { getProductReport } from '@/api/product';
import { ERR_OK } from '@/api/config'
import Scroll from '@/base/scroll/scroll';
import LoadMore from '@/base/load-more/load-more';

export default {
  data() {
    return {
      reportList: [],
      // loading ...
      over: false,
      isLoading: false
    };
  },
  created() {
    this.limit = 20;
    this.offset = 0;

    this._getReport();
  },
  methods: {
    _getReport() {
      this.isLoading = true;

      const id = this.$route.params.id;
      const limit = this.limit;
      const offset = this.offset;

      getProductReport({ id, limit, offset }).then((res) => {
          if (res.data.code === ERR_OK) {
            this.reportList.push(...res.data.data.list);

            this.isLoading = false;

            if (res.data.data.list.length < this.limit) {
              this.over = true;
            }
          } else {
            this.$dialog.alert({
              mes: res.data.msg,
              callback() {}
            });
          }
        }).catch((err) => {
          console.log(err);
        });
    },
    getMore() {
      if (this.over) {
        return;
      }

      this.offset += 20;
      this._getReport();
    }
  },
  components: {
    Scroll,
    LoadMore
  }
};
</script>

<style scoped lang="stylus">
@import "~@/common/stylus/variable";

.report-history
  position fixed
  top 0
  right 0
  bottom 0
  left 0
  background-color $color-bg
  .header
    display flex
    justify-content space-between
    position relative
    padding 0.1rem 0.3rem
    background-color #fff
    box-shadow 0 0.01rem 0.05rem 0.03rem #eee
    z-index 20
    span
      font-size 0.4rem
  .report-list-wrapper
    position absolute
    width 100%
    height calc(100% - 0.4rem)
    overflow hidden
    .report-list
      padding-bottom 0.2rem
      .item
        display flex
        justify-content space-between
        align-items center
        padding 0.2rem 0.3rem
        background-color #fff
        font-size 0.32rem
        .link, .link:visited
          color $color-link
  .tips
    text-align center
    line-height 2rem
    font-size 0.4rem
    color $color-txt-grey
</style>



